:root {
    /* colors */
    --headerBgColor: #ffffff;
    --primaryBackgroundColor: #ffffff;
    --secondaryBackgroundColor: #f8f8f8;
    /* animation speeds */

    /* widths, height */
    --sidebarWidth: 250px;
    --sidebarMinWidth: 60px;
    --headerHeight: 70px;
}

.header_container {
    display: flex;
    background-color: var(--headerBgColor);
    width: calc(100vw - var(--sidebarWidth));
    height: var(--headerHeight);
    font-size: 14px;
    align-items: center;
}

.header_search {
    display: flex;
    width: 50%;
}

.header_search input[type='text'] {
    margin-left: 25px;
    padding: 10px;
    padding-left: 25px;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    border-bottom-left-radius: 40px;
    border: 1px solid #ccc;
    font-family: 'Poppins', sans-serif;
    transition: all 0.4s;
}

.header_search input[type='text']:focus {
    outline: none;
    padding-right: 100px;
    padding-left: 25px;
    border: 2px solid rgb(136, 136, 136);
}

.header_right {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.header_right span {
    margin-right: 20px;
}

.header_right i {
    font-size: 26px;
    color: #a4a4a4;
}

.header_right #last {
    margin-right: 38px;
}

.header_right #first {
    margin-right: -10px;
    cursor: pointer;
}

#header_profile {
    /* width: 100%;
      height: 100%; */
}

#header_profile img {
    width: 38px;
    height: 38px;
    border-radius: 1000px;
    cursor: pointer;
}

.header_right span #user_name {
    font-size: 22px;
    color: #333;
    cursor: pointer;
    margin-left: 10px;
}

.header_right .notification {
    position: absolute;
    width: 25px;
    height: 25px;
    background-color: #fb9678;
    border-radius: 50%;
    text-align: center;
    line-height: 25px;
    color: white;
    top: 10px;
    right: 100px;
    box-shadow: -5px 7px 10px 0 rgba(51, 51, 51, 0.21);
    cursor: pointer;
}

.header_right .bell {
    cursor: pointer;
}

.header_right .bell:hover~#bell_menu,
.header_right .notification:hover~#bell_menu,
.header_right #bell_menu:hover {
    top: 60px;
    visibility: visible;
}

.header_right #bell_menu {
    background-color: var(--primaryBackgroundColor);
    visibility: hidden;
    display: flex;
    flex-direction: column;
    width: 280px;
    position: absolute;
    top: 150px;
    right: 100px;
    box-shadow: 0px 0px 15px 1px rgba(69, 65, 78, 0.2);
    border-radius: 12px;
    transition: all 0.6s cubic-bezier(0.18, 0.89, 0.17, 1.48);
}

.header_right #bell_menu #bell_menu_header {
    background-color: var(--primaryBackgroundColor);
    text-align: center;
    font-size: 22px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

.header_right #bell_menu #bell_menu_card {
    background-color: var(--secondaryBackgroundColor);
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

#bell_menu #bell_menu_arrow_top {
    position: relative;
    left: 230px;
    top: -10px;
}

.header_right #bell_menu #bell_menu_card ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    margin: 20px auto;
    padding: 0;
    width: 80%;
}

.header_right #bell_menu #bell_menu_card ul li {
    background-color: var(--primaryBackgroundColor);
    box-shadow: 0 0 10px 0 rgba(58, 58, 58, 0.1);
    padding: 10px 20px;
    margin: 5px 0;
    border-radius: 10px;
    cursor: pointer;
}

.header_right #profile_menu {
    visibility: hidden;
    display: flex;
    flex-direction: column;
    background-color: var(--secondaryBackgroundColor);
    width: 280px;
    position: absolute;
    top: 150px;
    right: 150px;
    box-shadow: 0px 0px 15px 1px rgba(69, 65, 78, 0.2);
    border-radius: 12px;
    transition: all 0.6s cubic-bezier(0.18, 0.89, 0.17, 1.48);
}

.header_right #profile_menu #profile_menu_header {
    font-size: 16px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 10px;
}

.header_right #profile_menu #profile_menu_header img {
    width: 75px;
    height: 75px;
    margin: auto 10px;
    margin-right: 30px;
}

.header_right #profile_menu #profile_menu_header #profile_caption {
    position: relative;
    font-size: 12px;
    color: #969696;
    top: -30px;
    right: -115px;
}

.header_right #profile_menu #profile_menu_card ul {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    background-color: var(--secondaryBackgroundColor);
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 20px 20px;
    box-sizing: border-box;
}

.header_right #profile_menu #profile_menu_card ul li {
    cursor: pointer;
    margin-left: 15px;
    cursor: pointer;
    margin-bottom: 20px;
}

.header_right #profile_menu #profile_menu_card .profile_text {
    margin-left: 10px;
}

.header_right #profile_menu #profile_menu_bottom {
    padding: 23px 0px;
}

.header_right #profile_menu #profile_menu_bottom button {
    float: right;
    margin-right: 20px;
}

#profile_menu #profile_menu_arrow_top {
    position: relative;
    left: 230px;
    top: -10px;
}

.header_right #first:hover~#header_profile #profile_menu,
#header_profile>img:hover~#profile_menu,
.header_right #profile_menu:hover {
    visibility: visible;
    top: 60px;
}

.header_right #grid_menu {
    visibility: hidden;
    display: flex;
    flex-direction: column;
    background-color: var(--primaryBackgroundColor);
    width: 280px;
    position: absolute;
    top: 150px;
    right: 58px;
    box-shadow: 0px 0px 15px 1px rgba(69, 65, 78, 0.2);
    border-radius: 12px;
    transition: all 0.6s cubic-bezier(0.18, 0.89, 0.17, 1.48);
}

.header_right #grid_menu #grid_menu_header {
    font-size: 20px;
    margin: 10px 0;
    text-align: center;
}

.header_right #grid_menu #grid_menu_card ul {
    border-top: 1px solid #DDD;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style-type: none;
    background-color: var(--secondaryBackgroundColor);
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.header_right #grid_menu #grid_menu_card ul li {
    cursor: pointer;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 50%;
    border: 1px solid #EEE;
    box-sizing: border-box;
    padding: 30px;
}

.header_right #grid_menu #grid_menu_card ul li i {
    font-size: 34px;
}

.header_right #grid_menu #grid_menu_card ul li:hover {
    background-color: #EEEEEE;
}

.header_right #grid_menu .grid_text {
    color: #3F51B5;
    margin-right: 0;
    margin-top: 10px;
    font-size: 12px;
}

#grid_menu #grid_menu_arrow_top {
    position: relative;
    left: 230px;
    top: -10px;
}

.header_right #grid {
    cursor: pointer;
}

.header_right #grid:hover~#grid_menu,
.header_right #grid_menu:hover {
    visibility: visible;
    top: 60px;
}

.header_right #last {
    cursor: pointer;
}